import React,{Component} from 'react';
import {Jumbotron, Container, Row, Col, CardGroup, Button, Media, Carousel, Card} from 'react-bootstrap';
import { HashLink as Link, NavHashLink as NavLink } from 'react-router-hash-link';
import map from './map.png';
class JourneyMap extends Component {
    constructor(props){
        super(props)
        this.state={
            mapPosition:0,
            mapClass:'map',
            positionMsg:"Todo comienza con la validación Inicial. En esta etapa deberás presentar todos los documentos que se solicitan a continuaciòn:"
            
        }
        this.mapNextLocation = this.mapNextLocation.bind(this)
    }

    mapNextLocation(){
        
        console.log(this.state.mapPosition)
        
        if(this.state.mapPosition<2){
            const nextPos = this.state.mapPosition+1

            if(nextPos<=3){

                switch(nextPos){
                    case 0:
                    this.setState({
                        mapClass:"map",
                        mapPosition:0,
                        positionMsg:"Todo comienza con la validación Inicial. En esta etapa deberás presentar todos los documentos que se solicitan a continuaciòn:"
                        
                    })
                    break
    
                    case 1:
                    this.setState({
                                mapClass:"mapSecondPosition",
                                mapPosition:1,
                                positionMsg:"Analiza todo lo que hizo la competencia ¿En qué aspectos sos mejor, qué tenemos que mejorar?"
                    })
                    break
    
                    case 2:
                            this.setState({
                                mapClass:"mapThirdPosition",
                                mapPosition:2,
                                positionMsg:"¿Estás Listo para comenzar a construir tu Solución Digital :)"
                    })

    
                
            
                }
                }else{
                        this.setState({
                        mapPosition:0
                    })
                }
        }
    }



    render(){
        return(
            <Container>
                <Row>
                        <Col className="map-section">
                            <h5 id="#CrearSD"className="template-selection-title">CREA UNA APP DESDE CERO</h5>
                            <p className="map-description-text">{this.state.positionMsg}</p>
                            <div className="map-container">
                               
                                <img src={map} className={this.state.mapClass} />
                            </div>
                            <button className="map-btn" onClick={this.mapNextLocation}>Next </button>
                        </Col>
                    </Row>
                    <Row>
                    <Col  md={12}>
                        <button className="article-back-btn"><Link className="button-link" to="/">Volver</Link></button>
                    </Col>
                
            </Row>
            </Container>

        )
    }

}

export default JourneyMap